body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#header{
  background-image: linear-gradient(#48C6EF, #6F86D6);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;

}
#header-topic{
  margin-top:5%;
  margin-left: 0.5%;
}
#header-img1{
width: 70%;
margin-top: 15%;
margin-left: 5%;

}
#header-img2{
  width: 1080px;
  position: absolute;
  top:2%;
  right:5%;
}
.custom-shape-divider-bottom-1624519784 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-bottom-1624519784 svg {
  position: relative;
  display: block;
  width: calc(187% + 1.3px);
  height: 232px;
}

.custom-shape-divider-bottom-1624519784 .shape-fill {
  fill: #FFFFFF;
}

/** For mobile devices **/
@media (max-width: 767px) {
  .custom-shape-divider-bottom-1624519784 svg {
      width: calc(187% + 1.3px);
      height: 149px;
  }
}